.body {
  text-align: center;
  background-image: url("skyTwo.c60ddfd2.jpg");
  font-family: Arial, sans-serif;
}

.timer {
  max-width: 400px;
  background-color: #fff;
  border-radius: 10px;
  justify-content: center;
  gap: 10px;
  margin: 20px auto;
  padding: 20px;
  display: flex;
  box-shadow: 0 0 10px rgba(0, 0, 0, .2);
}

.fields {
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.field {
  text-align: center;
}

.value {
  color: pink;
  font-size: 24px;
}

.label {
  color: plum;
  font-size: 14px;
}

.unicorn-image {
  margin-bottom: 20px;
}

.unicorn-image img {
  width: 500px;
  height: auto;
  margin: 0 auto;
  display: block;
}

.dayContainer {
  background-color: #fff0f5;
  font-family: Poltawski Nowy;
}

.flatpickr-input {
  color: #9370db;
  height: 30%;
  background-color: #f8f8ff;
  border: 1px solid plum;
  font-family: Poltawski Nowy;
  font-size: 16px;
}

.flatpickr-current-month {
  font-family: Poltawski Nowy;
}

.flatpickr-prev-month, .flatpickr-next-month {
  cursor: pointer;
  background-color: pink;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
}

.flatpickr-weekdays {
  text-transform: uppercase;
  background-color: ivory;
  font-family: Poltawski Nowy;
  font-weight: bold;
}

.uni-btn {
  color: #713ae0;
  height: 25px;
  cursor: pointer;
  border: 1px solid plum;
  border-radius: 50%;
  font-family: Poltawski Nowy;
}

.uni-btn:hover {
  color: #bdace0;
}

